import React from 'react';
import { List } from 'antd-mobile';

const Item = List.Item;

interface IState {
    [key: string]: any;
}

export default class TextareaItem extends React.Component<IProps, IState> {
    render() {
        const {
            icon,
            field,
            value = '',
            rows,
            placeholder,
            onChange,
            disabled,
            arrow,
        } = this.props;
        return (
            <Item arrow={arrow}>
                <div className="hyList-item">
                    <div className="hyList-item-img">
                        <img
                            src={icon}
                            style={icon ? {} : { visibility: 'hidden' }}
                        />
                    </div>
                    <div className="hyList-item-desc">
                        {disabled ? (
                            <p>{value}</p>
                        ) : (
                            <textarea
                                rows={rows}
                                value={value}
                                placeholder={placeholder}
                                onChange={(e) => {
                                    onChange && onChange(field, e.target.value);
                                }}
                            ></textarea>
                        )}
                    </div>
                </div>
            </Item>
        );
    }
}
