import React, { useContext } from 'react';
import { MeetingDetailStore } from './store';
import MeetingNotificationDetailClass from './detail/MeetingNotificationDetail';
import EasyNarBar from '@/components/EasyNarBar';
import { TokenContext } from '@/pages/_internal/app/middlewares/AppAuthorized';
require('./detail/style.scss');

const MeetingNotificationDetail = (props: any) => {
    const { match } = props;
    const tokenContext = useContext(TokenContext);
    const meetingDetailStore = new MeetingDetailStore({
        history: props.history,
        easyNarBar: EasyNarBar,
        tokenContext,
    });
    return (
        <MeetingNotificationDetailClass
            store={meetingDetailStore}
            params={match.params}
        />
    );
};

export default MeetingNotificationDetail;
