import React from 'react';
import dayjs from 'dayjs';
import { List, DatePicker } from 'antd-mobile';
import Content from './Content';

const Item = List.Item;

interface IState {
    [key: string]: any;
}

export default class PickerItem extends React.Component<IProps, IState> {
    render() {
        const {
            icon,
            field,
            placeholder,
            onChange = () => {},
            disabled,
            extra,
            arrow,
            value,
            minDate,
            minuteStep,
            formatValue,
        } = this.props;
        return (
            <DatePicker
                minDate={minDate}
                value={value || null}
                onChange={(date) => {
                    onChange && onChange(field, dayjs(date).format());
                }}
                minuteStep={minuteStep || 1}
                disabled={disabled}
            >
                <Item className="hyList-noExtra" extra={extra} arrow={arrow}>
                    <Content
                        icon={icon}
                        value={
                            formatValue && value ? formatValue(value) : value
                        }
                        placeholder={placeholder}
                    />
                </Item>
            </DatePicker>
        );
    }
}
