import React, { useContext, useState, useEffect } from "react";
import { observer } from "mobx-react";
import { Icon, Toast } from "antd-mobile";
import MeetingNotificationContent from "./components/MeetingNotificationContent";
import WxApiContext from "@/service/wx-api";
import SubPage from "@/components/SubPage";
import storage from "@/utils/storage";
import getDistance from "@/utils/tools/getDistance";

export default observer((props: any) => {
  const { store, params } = props;
  const { signIn } = store;
  const history = store.history;
  const wxContext: any = useContext(WxApiContext);
  const { wxApi } = wxContext;
  const url = window.location.href;
  const api = wxApi(url);
  const [detail, setDetail] = useState({ id: null, latitude: null, longitude: null, scope: 0 });

  const getDetail = () => {
    if (params && params.id) {
      store.fetchDetail(params.id).then((data: any) => {
        setDetail(data);
      });
    }
  };

  const onSignIn = (type: string) => {
    const { latitude, longitude, scope } = detail;
    checkSignRange(0);
  };

  const checkHasToBase64 = () => {
    const { checkJsApi } = api.base.checkJsApi;
    return new Promise((resolve, reject) => {
      checkJsApi({
        jsApiList: ["getLocalImgData"],
        success: function (res: any) {
          if (res.checkResult && res.checkResult.getLocalImgData) {
            resolve(true);
          } else {
            reject(false);
          }
        },
        fail: function () {
          reject(false);
        }
      });
    });
  };

  const checkSignRange = (retry: number) => {
    const { latitude, longitude } = detail;
    let { scope } = detail;
    const { getLocation } = api.location;
    retry = retry || 0;

    if (scope < 20) {
      scope = 20;
    }

    getLocation({
      type: "gcj02",
      success: (res: any) => {
        if (res.latitude && res.longitude) {
          let c = new qq.maps.LatLng(Number(res.latitude), Number(res.longitude));
          let d = new qq.maps.LatLng(Number(latitude), Number(longitude));
          let g = qq.maps.geometry.spherical.computeDistanceBetween(c, d);
          g = Math.floor(g);
          console.log("当前位置 -- ", res.latitude, res.longitude);
          console.log("签到位置 -- ", Number(latitude), Number(longitude));
          console.log(`第${retry}次测距`, Math.abs(g), Number(scope));
          if (Math.abs(g) <= Number(scope)) {
            console.log("距离校验成功");
            checkHasToBase64()
              .then(() => {
                signIn(api.media, detail, true, () => {
                  setTimeout(() => {
                    if (history.length === 1) {
                      history.push({ pathname: "/meeting-notification" });
                    } else {
                      history.goBack();
                    }
                    storage.setItem("meetingNotificationTab", "1");
                  }, 1000);
                });
              })
              .catch(() => {
                signIn(api.media, detail, false, () => {
                  setTimeout(() => {
                    if (history.length === 1) {
                      history.push({ pathname: "/meeting-notification" });
                    } else {
                      history.goBack();
                    }
                    storage.setItem("meetingNotificationTab", "1");
                  }, 1000);
                });
              });
          } else if (Math.abs(Math.abs(g) - Number(scope)) <= 50) {
            console.log("retry -- ", retry);
            if (retry < 3) {
              console.log("距离误差在50米以内，重新调用函数", retry);
              checkSignRange(retry + 1);
            } else {
              Toast.info(`定位误差在${Math.round(Math.abs(Math.abs(g) - Number(scope)))}米内，请重试`, 1);
            }
          } else {
            Toast.info("超出可签到范围", 1);
          }
        }
      },
      fail: function () {
        Toast.info("获取当前定位失败", 1);
      }
    });
  };

  useEffect(() => {
    getDetail();
    store.getMemberName();
  }, []);

  useEffect(() => {
    return () => {
      store.reset();
      Toast.hide();
    };
  });

  return (
    <SubPage history={history} title="会议详情" bgColor="#fff" prePage="/meeting-notification">
      <MeetingNotificationContent data={detail} onSignIn={onSignIn} history={history} />
    </SubPage>
  );
});
