import React from 'react';
import classNames from 'classnames';

interface IProps {
    icon?: any;
    value?: any;
    placeholder?: string;
}

export default (props: IProps) => {
    const { icon, value, placeholder } = props;
    return (
        <div className="hyList-item">
            <div className="hyList-item-img">
                <img src={icon} style={icon ? {} : { visibility: 'hidden' }} />
            </div>
            <div
                className={classNames('hyList-item-content', {
                    'hyList-item-content_empty': !value,
                })}
            >
                {value || placeholder}
            </div>
        </div>
    );
};
