import React from 'react';
import { List } from 'antd-mobile';
import Content from './Content';

const Item = List.Item;

interface IState {
    [key: string]: any;
}

export default class JumpItem extends React.Component<IProps, IState> {
    render() {
        const {
            icon,
            field,
            value = '',
            placeholder,
            onClick,
            disabled,
            arrow,
        } = this.props;
        return (
            <Item
                arrow={arrow}
                onClick={() => {
                    if (!disabled && onClick) {
                        onClick();
                    }
                }}
            >
                <Content icon={icon} value={value} placeholder={placeholder} />
            </Item>
        );
    }
}
