import React from 'react';
import { List, Picker } from 'antd-mobile';
import Content from './Content';

const Item = List.Item;

interface IState {
    [key: string]: any;
}

export default class PickerItem extends React.Component<IProps, IState> {
    render() {
        const {
            icon,
            field,
            data = [],
            title,
            cols,
            values = [],
            placeholder,
            onChange = () => {},
            disabled,
            extra,
            arrow,
        } = this.props;
        return (
            <Picker
                title={title}
                data={data}
                cols={cols}
                value={values}
                onChange={onChange}
                disabled={disabled}
            >
                <Item extra={extra} arrow={arrow}>
                    <Content
                        icon={icon}
                        value={title}
                        placeholder={placeholder}
                    />
                </Item>
            </Picker>
        );
    }
}
