import React, { useContext, useEffect } from "react";
import dayjs from "dayjs";
import { List, Button, WingBlank, WhiteSpace, Toast } from "antd-mobile";
import HyList from "@/components/HyList/List";
import { TokenContext } from "@/pages/_internal/app/middlewares/AppAuthorized";
import storage from "@/utils/storage";
import icon1 from "@/assets/imgs/meeting/message_icon1.png";
import icon2 from "@/assets/imgs/meeting/message_icon2.png";
import icon3 from "@/assets/imgs/meeting/message_icon3.png";
import icon4 from "@/assets/imgs/meeting/message_icon4.png";
import icon5 from "@/assets/imgs/meeting/message_icon5.png";
import icon6 from "@/assets/imgs/meeting/message_icon6.png";
import { inject } from "mobx-react";

const Item = List.Item;
const { InputItem, TextareaItem, JumpItem } = HyList;
const week = ["日", "一", "二", "三", "四", "五", "六"];

export default inject("userInfoStore")((props: any) => {
  const tokenContext = useContext(TokenContext);
  const { data, onSignIn } = props;
  const {
    avoidLaterTime,
    confereeId,
    id,
    latitude,
    longitude,
    meetingDescribe,
    meetingSponsor,
    name,
    startTime,
    workNumber,
    endTime,
    type,
    signinType,
    nowAddress,
    roomName,
    specificAddress,
    cancelstate,
    singinTime
  } = data;

  const formatDate = (start: any, end?: any) => {
    return `${dayjs(start).format("YYYY年MM月DD日")} 周${week[dayjs(start).day()]} ${dayjs(start).format("HH:mm")}${
      end ? "-" + dayjs(end).format("HH:mm") : ""
    }`;
  };

  const getSigninTime = () => {
    const {
      userInfoStore: { userMemberInfo }
    } = props;
    const { WorkNumber } = userMemberInfo || {};

    if (workNumber && singinTime) {
      let arr1 = workNumber.split(",");
      let arr2 = singinTime.split(",");
      let curIndex = arr1.findIndex((item: any) => item === WorkNumber);
      if (curIndex >= 0) {
        return arr2[curIndex];
      }
      return null;
    }
  };

  const checkSignIn = () => {
    const {
      userInfoStore: { userMemberInfo }
    } = props;
    const { WorkNumber } = userMemberInfo || {};

    if (workNumber && signinType) {
      let arr1 = workNumber.split(",");
      let arr2 = signinType.split(",");
      let curIndex = arr1.findIndex((item: any) => item === WorkNumber);
      if (curIndex >= 0 && Number(arr2[curIndex]) === 1) {
        return true;
      }
      return false;
    }
  };

  const checkSignInTime = () => {
    const {
      data: { startTime, endTime, checkinDelay, checkinEarly }
    } = props;
    if (Number(checkinDelay) === 0) {
      if (Number(checkinEarly) === 0) {
        if (dayjs(startTime).isAfter(dayjs())) {
          return "未到签到时间";
        } else if (dayjs(endTime).isBefore(dayjs())) {
          return "会议已结束";
        }
      } else if (
        dayjs(startTime)
          .subtract(Number(checkinEarly) || 0, "minute")
          .isAfter(dayjs())
      ) {
        return "未到签到时间";
      } else if (dayjs(endTime).isBefore(dayjs())) {
        return "会议已结束";
      } else if (dayjs(startTime).isBefore(dayjs())) {
        return "会议已开始";
      }
    }
    let start = dayjs(startTime).subtract(Number(checkinEarly) || 0, "minute");
    let end = dayjs(startTime).add(Number(checkinDelay) || 0, "minute");
    if (dayjs().isBefore(start)) {
      return "未到签到时间";
    } else if (dayjs().isAfter(dayjs(endTime))) {
      return "会议已结束";
    } else if (dayjs().isAfter(end)) {
      return "签到已结束";
    }

    return 0;
  };

  const returnSignIn = () => {
    if (cancelstate === "1") {
      return (
        <Button type="primary" disabled>
          会议已取消
        </Button>
      );
    }
    if (checkSignIn()) {
      let time = getSigninTime();
      return (
        <div>
          <Button type="primary" disabled>
            已签到
          </Button>
          <div>{time ? `签到时间: ${time}` : ""}</div>
        </div>
      );
    }
    let state = checkSignInTime();
    if (state) {
      return (
        <Button type="primary" disabled>
          {state}
        </Button>
      );
    }
    if (Number(type) === 0) {
      return (
        <Button type="primary" disabled>
          请在会议室进行签到
        </Button>
      );
    }
    return (
      <Button type="primary" onClick={onSignIn}>
        签到
      </Button>
    );
  };

  const jumpSelectLocation = (latlng: any) => {
    props.history.push({
      pathname: "/choose_address",
      state: {
        pathname: `/meeting-notification/${id}`,
        params: {
          latlng
        }
      }
    });
  };

  useEffect(() => {
    const {
      userInfoStore: { fetchUserInfo }
    } = props;
    fetchUserInfo();
  }, []);

  return (
    <div className="meeting-notification-detail" style={{ display: id ? "block" : "none" }}>
      <List>
        <div className="meeting-notification-detail-section">
          <InputItem icon={icon1} disabled={true} value={name || "无"} />
          <TextareaItem icon={icon2} rows={3} value={meetingDescribe || "无"} disabled={true} />
        </div>
        {Number(type) === 1 && (
          <div className="meeting-notification-detail-section">
            {startTime && endTime && <InputItem icon={icon3} disabled={true} value={formatDate(startTime, endTime)} />}
            <JumpItem
              icon={icon4}
              value={nowAddress}
              arrow="horizontal"
              disabled={false}
              onClick={() => {
                jumpSelectLocation({
                  lat: latitude,
                  lng: longitude
                });
              }}
            />
            <InputItem disabled={true} value={specificAddress || "无"} />
          </div>
        )}
        {Number(type) === 0 && (
          <div className="meeting-notification-detail-section">
            <InputItem icon={icon4} disabled={true} value={roomName || "无"} />
            {startTime && endTime && <InputItem icon={icon3} disabled={true} value={formatDate(startTime, endTime)} />}
          </div>
        )}
        <div className="meeting-notification-detail-section">
          <InputItem icon={icon5} disabled={true} value={`发起人:${meetingSponsor || "无"}`} />
          <InputItem
            icon={icon6}
            disabled={true}
            value={
              "参与人: " +
              `${signinType ? signinType.split(",").filter((item: any) => Number(item) === 1).length : 0}人已签到, ${
                signinType ? signinType.split(",").filter((item: any) => Number(item) === 0).length : 0
              }人未签到`
            }
          />
        </div>
      </List>
      <WhiteSpace size="xl" />
      <WingBlank size="lg">{returnSignIn()}</WingBlank>
      {/* <Button type="primary" onClick={onSignIn}>
                    签到
                </Button> */}
    </div>
  );
});
