import React from "react";
import { List } from "antd-mobile";
import InputItem from "./InputItem";
import TextareaItem from "./TextareaItem";
import PickerItem from "./PickerItem";
import DatePickerItem from "./DatePickerItem";
import JumpItem from "./JumpItem";
require("./style.scss");

interface IState {
  [key: string]: any;
}

class HyList extends React.Component<IProps, IState> {
  static InputItem: typeof InputItem;
  static TextareaItem: typeof TextareaItem;
  static PickerItem: typeof PickerItem;
  static DatePickerItem: typeof DatePickerItem;
  static JumpItem: typeof JumpItem;
  render() {
    return <List>{this.props.children}</List>;
  }
}

HyList.InputItem = InputItem;
HyList.TextareaItem = TextareaItem;
HyList.PickerItem = PickerItem;
HyList.DatePickerItem = DatePickerItem;
HyList.JumpItem = JumpItem;

export default HyList;

export { InputItem, TextareaItem, PickerItem, DatePickerItem, JumpItem };
