import React from 'react';
import { List } from 'antd-mobile';
import Content from './Content';

const Item = List.Item;

interface IState {
    [key: string]: any;
}

export default class InputItem extends React.Component<IProps, IState> {
    render() {
        const {
            icon,
            field,
            value = '',
            placeholder,
            onChange,
            disabled,
            arrow,
        } = this.props;
        return (
            <Item arrow={arrow}>
                <Content
                    icon={icon}
                    value={(() => {
                        if (disabled) {
                            return value || '';
                        } else {
                            return (
                                <input
                                    type="text"
                                    value={value || ''}
                                    placeholder={placeholder}
                                    onChange={(e) => {
                                        onChange &&
                                            onChange(field, e.target.value);
                                    }}
                                />
                            );
                        }
                    })()}
                    placeholder={placeholder}
                />
            </Item>
        );
    }
}
